import React from 'react';
import Index from './index'
import Content from '../components/content'
import {graphql} from 'gatsby'
import PostInfo from '../components/contentComponents/postInfo'

const PostList = ({ data }) => {   
    const postListData = data.allMdx.edges
    return (
        <Index>
            <Content>
                {postListData.map((data,index)=>{
                    const title = data.node.frontmatter.title
                    const date = data.node.frontmatter.date
                    const content = data.node.excerpt
                    return (
                        <PostInfo key={index} title={title} date={date} content={content}></PostInfo>
                    )
                })}
            </Content>
        </Index>
    )
};

export default PostList;

export const pageQuery = graphql`
query CategoryPostBySlug($category: String) {
    site {
      siteMetadata {
        title
      }
    }
    allMdx(filter: {frontmatter: {category: {in: [$category]}}}) {
      edges {
        node {
          frontmatter {
            title
            date
          }
        excerpt
        }
      }
    }
  }
  `